import Vue from 'vue'

Vue.prototype.$pageSizes = [
    {label:'10',value:10},
    {label:'15',value:15},
    {label:'20',value:20},
    {label:'30',value:30},
    {label:'50',value:50},
    {label:'100',value:100},
    {label:'500',value:500},
    {label:'1000',value:1000},
    {label:'5000',value:5000},
    {label:'10000',value:10000}
]

Vue.prototype.$checkSelect = function (arr) {
    if(arr.length == 0){
        Vue.prototype.$message({
            showClose: true,
            message: '',
            type: 'info'
        })
        return false
    }else{
        return true
    }
}